import React from "react";
import Calon from "../components/Calon";

export default function DataCalon() {
  return (
    <div className="d-flex justify-content-around m-3">
      <div className="col m-auto">
        <Calon />
        <Calon />
      </div>
      <div className="col m-2">
        <form>
          <h3>Input Calon Baru</h3>
          <input type="text" class="form-control m-2" placeholder="Nama" />
          <input type="tel" class="form-control m-2" placeholder="NIM" />
          <label for="exampleFormControlFile1">Gambar Calon</label>
          <input
            type="file"
            className="form-control-file m-2"
            id="exampleFormControlFile1"
          />
          <button className="btn btn-primary m-2" type="submit">
            Submit
          </button>
        </form>
      </div>
    </div>
  );
}
