import React from "react";

export default function Navbar() {
  return (
    <nav className="sb-topnav navbar navbar-expand navbar-dark bg-dark d-flex">
      <button className="btn btn-link navbar-brand">Admin</button>
      <button
        className="btn btn-link btn-sm order-1 order-lg-0"
        id="sidebarToggle"
      >
        <i className="fas fa-bars"></i>
      </button>
      <ul className="navbar-nav ml-auto ml-md-0">
        <li className="nav-item dropdown">
          <button
            className="btn nav-link dropdown-toggle"
            id="userDropdown"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <i className="fas fa-user fa-fw"></i>
          </button>
          <div
            className="dropdown-menu dropdown-menu-right"
            aria-labelledby="userDropdown"
          >
            <button className="btn btn-link dropdown-item">Settings</button>
            <button className="btn btn-link dropdown-item">Activity Log</button>
            <div className="dropdown-divider"></div>
            <button className="btn btn-link dropdown-item">Logout</button>
          </div>
        </li>
      </ul>
    </nav>
  );
}
