import React from "react";
import Pemilih from "./Pemilih";
import { useEffect } from "react";

export default function Table() {
  useEffect(() => {
    const dataTable = document.createElement("script");
    dataTable.innerHTML = " $('#dataTable').DataTable()";

    document.body.appendChild(dataTable);

    return () => {
      document.body.removeChild(dataTable);
    };
  }, []);
  return (
    <div className="card mb-4">
      <div className="card-header">
        <i className="fas fa-table mr-1"></i>
        Daftar Pemilih
      </div>
      <div className="card-body">
        <div className="table-responsive">
          <table
            className="table table-bordered"
            id="dataTable"
            width="100%"
            cellspacing="0"
          >
            <thead>
              <tr>
                <th>Nama</th>
                <th>NIM</th>
                <th>Angkatan</th>
                <th>Status</th>
              </tr>
            </thead>
            <tfoot>
              <tr>
                <th>Nama</th>
                <th>NIM</th>
                <th>Angkatan</th>
                <th>Status</th>
              </tr>
            </tfoot>
            <tbody>
              <Pemilih name="Hanif" />
              <Pemilih name="Dipta" />
              <Pemilih name="Jason" />
              <Pemilih name="Jeje" />
              <Pemilih name="Fauzan" />
              <Pemilih name="Hanif" />
              <Pemilih name="Dipta" />
              <Pemilih name="Jason" />
              <Pemilih name="Jeje" />
              <Pemilih name="Fauzan" />
              <Pemilih name="Hanif" />
              <Pemilih name="Dipta" />
              <Pemilih name="Jason" />
              <Pemilih name="Jeje" />
              <Pemilih name="Fauzan" />
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
