import React from "react";
import Mainboard from "./pages/Mainboard";
import DashboardAdmin from "./pages/DashboardAdmin";
import SetelanPemilihan from "./pages/SetelanPemilihan";
import DataPemilih from "./pages/DataPemilih";
import DataCalon from "./pages/DataCalon";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Footer from "./components/Footer";
import Navbar from "./components/Navbar";
import Sidebar from "./components/Sidebar";

function App() {
  return (
    <Router>
      <div className="sb-nav-fixed">
        <Navbar />
        <div id="layoutSidenav">
          <Sidebar />
          <div id="layoutSidenav_content">
            <Switch>
              <Route path="/" exact component={Mainboard} />
              <Route path="/data-calon" component={DataCalon} />
              <Route path="/data-pemilih" component={DataPemilih} />
              <Route path="/admin" component={DashboardAdmin} />
              <Route path="/layout-pemilihan" component={DashboardAdmin} />
              <Route path="/setelan-pemilihan" component={SetelanPemilihan} />
            </Switch>
            <Footer />
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;
