import React from "react";

export default function SetelanPemilihan() {
  return (
    <form class="form-check m-2">
      <h2>Setelan Pemilihan </h2>
      <input
        class="form-check-input "
        type="checkbox"
        value=""
        id="defaultCheck1"
      />
      <label class="form-check-label" for="defaultCheck1">
        Abstain
      </label>
    </form>
  );
}
