import React from "react";

export default function Pemilih(props) {
  return (
    <tr>
      <td>
        <button
          className="btn btn-link"
          data-toggle="modal"
          data-target="#exampleModal"
        >
          {props.name}
        </button>
      </td>
      <td>1010101</td>
      <td>2019</td>
      <td>Telah Memilih</td>
    </tr>
  );
}
