import React from "react";
import SidebarButton from "./SidebarButton";
import SidebarDropdown from "./SidebarDropdown";

export default function Sidebar() {
  return (
    <div id="layoutSidenav_nav">
      <nav
        className="sb-sidenav accordion sb-sidenav-dark"
        id="sidenavAccordion"
      >
        <div className="sb-sidenav-menu">
          <div className="nav">
            <div className="sb-sidenav-menu-heading">Admin Dashboard</div>
            <SidebarButton
              title="Dashboard"
              iconclass="fas fa-tachometer-alt"
              link="/"
            />
            <SidebarButton
              title="Admin"
              iconclass="fas fa-user-cog"
              link="/admin"
            />
            <SidebarDropdown
              title="Data"
              target="Data"
              iconclass="fas fa-columns"
            />
            <div
              className="collapse"
              id="collapseData"
              aria-labelledby="headingOne"
              data-parent="#sidenavAccordion"
            >
              <nav className="sb-sidenav-menu-nested nav">
                <SidebarButton
                  title="Data Calon"
                  iconclass="far fa-address-card"
                  link="/data-calon"
                />
                <SidebarButton
                  title="Data Pemilih"
                  iconclass="fas fa-users"
                  link="/data-pemilih"
                />
              </nav>
            </div>
            <SidebarDropdown
              title="Pemilihan"
              target="Pemilihan"
              iconclass="fas fa-vote-yea"
            />
            <div
              className="collapse"
              id="collapsePemilihan"
              aria-labelledby="headingOne"
              data-parent="#sidenavAccordion"
            >
              <nav className="sb-sidenav-menu-nested nav">
                <SidebarButton
                  title="Layout Pemilihan"
                  iconclass="fas fa-border-all"
                  link="/layout-pemilihan"
                />
                <SidebarButton
                  title="Setelan Pemilihan"
                  iconclass="fas fa-cog"
                  link="/setelan-pemilihan"
                />
              </nav>
            </div>
          </div>
        </div>
        <div className="sb-sidenav-footer">
          <div className="small">Logged in as:</div>
          Admin
        </div>
      </nav>
    </div>
  );
}
