import React from "react";
import { Link } from "react-router-dom";

export default function SidebarButton(props) {
  return (
    <Link to={props.link} className="nav-link">
      <div className="sb-nav-link-icon">
        <i className={props.iconclass}></i>
      </div>
      {props.title}
    </Link>
  );
}
